	module.exports = {"data":{"site":{"siteMetadata":{"title":":-)","author":"github.com/9renpoto"}},"markdownRemark":{"id":"/home/travis/build/9renpoto/entry/src/pages/2013-03-12-riak-meetup-tokyo/index.md absPath of file >>> MarkdownRemark","html":"<h1>Riak intro &#x26; RICON報告</h1>\n<p>@kuenishi</p>\n<p>分散システムの研究や開発は5年ぐらい\n日本法人が9月に出来た。</p>\n<h2>Riakの紹介</h2>\n<ul>\n<li>\n<p>Dynamoにインスパイアされた</p>\n</li>\n<li>\n<p>Consistent Hassingによる水平分割する</p>\n</li>\n<li>\n<p>Vector Clocksを用いてレプリケーション</p>\n</li>\n<li>\n<p>Erlang/OTPで動作</p>\n</li>\n<li>\n<p>分散KVS</p>\n</li>\n<li>\n<p>HTTP / Protocol Buffers</p>\n</li>\n<li>\n<p>Secondary Index/ MapReduce</p>\n</li>\n<li>\n<p>Link Walking</p>\n</li>\n<li>\n<p>Consistent Hassing and Replicas / Vecteor Clocks</p>\n</li>\n</ul>\n<p>分散システム、ざっくり。</p>\n<ul>\n<li>Graph</li>\n<li>SQL</li>\n<li>Document</li>\n<li>Scale out</li>\n</ul>\n<h2>RICONについて</h2>\n<ul>\n<li>分散システム、分散DBのカンファレンス</li>\n</ul>\n<h3>事例</h3>\n<ul>\n<li>\n<p>Redis側でロックして、裏側はRiakで構築</p>\n<ul>\n<li>壊れても大丈夫なようにするのが鉄板のパターン</li>\n</ul>\n</li>\n</ul>\n<h2>面白かった話</h2>\n<ul>\n<li>Rak Pipe</li>\n<li>Introduction Yokozuna</li>\n<li>Building Riak CS on Riak</li>\n<li>Cloning the Cloud</li>\n<li>Bringing Consistency on Riak</li>\n</ul>\n<h2>Riak with Engine yard</h2>\n<ul>\n<li>\n<p>EC2上のGentoo</p>\n</li>\n<li>\n<p>すべてはChefで構築</p>\n</li>\n<li>\n<p>その上にアプリをデプロイ</p>\n</li>\n<li>\n<p>再起動も1から構築</p>\n</li>\n<li>\n<p>ssh / sudo可能</p>\n</li>\n<li>\n<p>何台、何度でも再現可能</p>\n</li>\n<li>\n<p>DB</p>\n<ul>\n<li>PostgressSQL</li>\n<li>MySQL</li>\n<li>Riak</li>\n</ul>\n</li>\n<li>\n<p>Riakはバックエンドでたてないとダメ</p>\n</li>\n<li>\n<p>Riak Searchを使って全文検索が可能</p>\n</li>\n<li>\n<p>今後、Yokozunaに移行予定</p>\n</li>\n</ul>","frontmatter":{"title":"riak meetup tokyoに参加してきました","date":"March 12, 2013"}}},"pathContext":{"path":"/entry/2013/03/02/riak-meetoup-tokyo/"}}

